
  import { defineComponent, onMounted, ref } from 'vue';
  import axios from 'axios';
  import { message } from 'ant-design-vue';
  import {Tool} from "@/util/tool";

  export default defineComponent({
    name: 'AdminCategory',
    setup() {
      const categorys = ref();
      const loading = ref(false);

      const columns = [

        {
          title: '名称',
          dataIndex: 'name'
        },
        {
          title: '排序',
          key: 'sort',
          dataIndex: 'sort'
        },
        {
          title: '操作',
          key: 'action',
          slots: { customRender: 'action' }
        }
      ];
      /**
       * 一级分类树， children属性是二级分类
       * [{
       *   id:"",
       *   name:"",
       *   children:[{
       *     id:"",
       *   name:""
       *   }]
       * }]
       */
      const level1 = ref();


      /**
       * 数据查询
       **/
      const handleQuery = () => {
        loading.value = true;
        axios.get("http://localhost:8888/category/search", {
          params: {

          }
        }).then((response) => {
          loading.value = false;
          const data = response.data;
          if (data.success){
            categorys.value = data.list;
            console.log("原始数组：" ,categorys.value);
            level1.value = [];
            level1.value = Tool.array2Tree(categorys.value,0);
            console.log("树形结构",level1.value);

          }else {
            message.error(data.message);
          }

        });
      };

      /**
       * 表格点击页码时触发
       */


      onMounted(() => {
        handleQuery();
      });

      // 编辑表单

      const category=ref({});
      const modalVisible = ref<boolean>(false);
      const modalLoading = ref<boolean>(false);
      const myedit = (record: any) => {
        modalVisible.value = true;
        category.value=Tool.copy(record);
      };

      const handleModalOk = () => {

        modalLoading.value = true;


        axios.post("http://localhost:8888/category/edit", category.value).then((response) => {
          const data = response.data;
          if (data.success){
            //成功后：
            modalVisible.value = false;
            modalLoading.value = false;

            //重新加载列表
            handleQuery();
          }else {
            modalLoading.value = false;
            message.error(data.message);
          }




        });

      };

      // 新增
      const myadd = () => {
        modalVisible.value = true;
        category.value={};
      };

      //删除
      const mydelete = (record: any) => {
        category.value=record;
        axios.post("http://localhost:8888/category/delete", category.value).then((response) => {

          //成功后重新加载列表
          handleQuery();


        });
      };

      //






      return {
        // categorys,

        columns,
        loading,

        modalVisible,
        modalLoading,
        myedit,
        myadd,
        mydelete,
        handleModalOk,
        category,
        level1,



      }
    }
  });
